@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
		'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
		sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
		monospace;
}
#root {
	display: flex;
	font-family: 'Roboto', Whitney, 'Helvetica Neue', Helvetica, Arial, sans-serif;
	text-align: center;
	background-color: #111111;
}

.App {
	background-image: url('/images/cover-plain.jpg');
	background-repeat: no-repeat;
	background-color: #111111;
	background-position: 50%;
	height: 100%;
	width: 100%;

	position: fixed;
	z-index: -1;
    color: white;
    justify-content: stretch;
	font-size: larger;
	
	display: grid;
	grid-template-rows: 12vh 88vh;
}

.main {
	display: flex;
	flex-direction: column;
	overflow-y: scroll;
	overflow-x: hidden;
	height: 100%;
}

@media only screen and (max-width: 700px) {
	.main section:first-child {
		padding-top: calc(50px + 5vh);
	}
}

.normal {
	font-weight: normal;
}

.bold {
	font-weight: bold;
}

.bolder {
	font-weight: bolder;
}

.underlined {
	text-decoration: underline;
}

/* Buttons */

.buttonSection {
	display: grid;
	grid-auto-columns: 1fr;
	grid-auto-flow: column;
	justify-content: center;
}

@media only screen and (max-width: 700px) {
	.buttonSection {
		grid-auto-rows: 1fr;
		grid-auto-flow: row;
	}
}

.main section a.download {
	margin: 10px 30px;
	border: 0;
	padding: 1em 75px;
	border-radius: 5px;
	cursor: pointer;
	box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.2);
	font-size: 14px;
	position: relative;
	top: -2px;
	white-space: nowrap;
	max-width: 10vw;
	display: flex;
	justify-content: center;
	font-weight: normal;
	text-shadow: none;
}

@media only screen and (max-width: 700px) {
	.download {
		margin: 10px 0;
	}
}

.main section a.download:hover {
	top: 0px;
}

.main section a.platform {
	color: black;
	background-color: white;
}

a.platform > svg {
	padding: 0 5px;
}

.main section a.web {
	color: white;
	background-color: #7289da;
}

